@media (width > 60em) {
  .col-2 {
    flex: 0 1 calc(50% - 1.5rem);
  }
}

@media (width > 60em) {
  .col-3 {
    flex: 0 1 calc(33.3333333333% - 1.5rem);
  }
}

.featured {
  padding-block: 6rem;
  background: #fafafa;
}
.featured .d-flex-layout {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}